import React from "react";
import "./About.css";

const About = () => {
	return (
		<div id="aboutContainer">
			<p>
				This space explorer site allows you to venture through all the planets
				of our solar system, providing you with all the data you need about each
				planet. You learn about the many spacecraft and probes that have
				travelled out into space and those which have landed and traversed
				cellestial bodies, and the potential for life in our solar system and
				beyond.
			</p>
		</div>
	);
};

export default About;
